"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Info, UserPlus, Vote, BarChart2, Settings } from "lucide-react"
import OverviewTab from "./overview-tab"
import RegisterTab from "./register-tab"
import VoteTab from "./vote-tab"
import ResultsTab from "./results-tab"
import AdminTab from "./admin-tab"

export default function CampaignTabs({
  campaignInfo,
  proposals,
  candidates,
  isConnected,
  userIsRegistered,
  userHasVoted,
  userIsCandidate,
  needsRegistration,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  canRegister,
  canVote,
  isOwner,
  address,
  campaignId,
  onUpdate,
}) {
  const params = useParams()
  const router = useRouter()
  const [activeTab, setActiveTab] = useState("overview")

  // Set the active tab based on URL params
  useEffect(() => {
    const tabParam = params?.tab
    if (tabParam && ["overview", "register", "vote", "results", "admin"].includes(tabParam)) {
      setActiveTab(tabParam)
    }
  }, [params])

  // Update URL when tab changes
  const handleTabChange = (value) => {
    setActiveTab(value)
    router.push(`/campaigns/${campaignId}/${value}`, { scroll: false })
  }

  // Handle registration completion
  const handleRegistrationComplete = () => {
    onUpdate()
    // Optionally switch to vote tab if during voting period
    if (isDuringVoting) {
      handleTabChange("vote")
    }
  }

  // Handle vote completion
  const handleVoteComplete = () => {
    onUpdate()
    // Optionally switch to results tab
    handleTabChange("results")
  }

  return (
    <Tabs value={activeTab} onValueChange={handleTabChange} className="space-y-6">
      <TabsList className="grid grid-cols-4 md:grid-cols-5 h-auto p-1">
        <TabsTrigger value="overview" className="flex items-center gap-2 py-2">
          <Info className="h-4 w-4" />
          <span className="hidden sm:inline">Overview</span>
        </TabsTrigger>

        {(isBeforeStart || (isDuringVoting && needsRegistration && !userIsRegistered)) && (
          <TabsTrigger value="register" className="flex items-center gap-2 py-2">
            <UserPlus className="h-4 w-4" />
            <span className="hidden sm:inline">Register</span>
          </TabsTrigger>
        )}

        {isDuringVoting && (
          <TabsTrigger value="vote" className="flex items-center gap-2 py-2">
            <Vote className="h-4 w-4" />
            <span className="hidden sm:inline">Vote</span>
          </TabsTrigger>
        )}

        <TabsTrigger value="results" className="flex items-center gap-2 py-2">
          <BarChart2 className="h-4 w-4" />
          <span className="hidden sm:inline">Results</span>
        </TabsTrigger>

        {isOwner && (
          <TabsTrigger value="admin" className="flex items-center gap-2 py-2">
            <Settings className="h-4 w-4" />
            <span className="hidden sm:inline">Admin</span>
          </TabsTrigger>
        )}
      </TabsList>

      <TabsContent value="overview" className="mt-6">
        <OverviewTab
          campaignInfo={campaignInfo}
          isOwner={isOwner}
          isBeforeStart={isBeforeStart}
          isDuringVoting={isDuringVoting}
          isAfterEnd={isAfterEnd}
          proposals={proposals}
          candidates={candidates}
        />
      </TabsContent>

      <TabsContent value="register" className="mt-6">
        <RegisterTab
          campaignInfo={campaignInfo}
          userIsRegistered={userIsRegistered}
          onRegister={handleRegistrationComplete}
          campaignId={campaignId}
        />
      </TabsContent>

      <TabsContent value="vote" className="mt-6">
        <VoteTab
          campaignInfo={campaignInfo}
          proposals={proposals}
          candidates={candidates}
          isConnected={isConnected}
          userIsRegistered={userIsRegistered}
          userHasVoted={userHasVoted}
          needsRegistration={needsRegistration}
          address={address}
          onVote={handleVoteComplete}
          campaignId={campaignId}
        />
      </TabsContent>

      <TabsContent value="results" className="mt-6">
        <ResultsTab campaignInfo={campaignInfo} proposals={proposals} candidates={candidates} isOwner={isOwner} />
      </TabsContent>

      <TabsContent value="admin" className="mt-6">
        <AdminTab campaignInfo={campaignInfo} campaignId={campaignId} onUpdate={onUpdate} />
      </TabsContent>
    </Tabs>
  )
}
