"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Clock, AlertCircle, Calendar, User, PlayCircle, StopCircle } from "lucide-react"
import { formatAddress, getVotingTypeText, getRestrictionText } from "@/lib/utils"
import { useContract } from "@/context/contract-context"
import { toast } from "sonner"
import { handleRpcError } from "@/lib/error-handler"

export default function CampaignHeader({
  campaignInfo,
  timeRemaining,
  isBeforeStart,
  isDuringVoting,
  needsStatusUpdate,
  actionLoading,
  actionError,
  onUpdateStatus,
  campaignId,
  isOwner,
}) {
  const { contract } = useContract()
  const [isUpdatingStatus, setIsUpdatingStatus] = useState(false)

  // Function to update campaign status
  const updateCampaignStatus = async () => {
    if (!contract || isUpdatingStatus) return

    try {
      setIsUpdatingStatus(true)

      // Call the contract's updateCampaignStatus function
      // Note: This function might not exist in your contract, you may need to implement it
      // or use a different approach to update the status
      const tx = await contract.updateCampaignStatus(campaignId)

      toast.loading("Updating campaign status...", {
        id: "update-status",
      })

      await tx.wait()

      toast.success("Campaign status updated successfully!", {
        id: "update-status",
      })

      // Refresh data
      onUpdateStatus()
    } catch (error) {
      console.error("Error updating campaign status:", error)
      handleRpcError(error)
    } finally {
      setIsUpdatingStatus(false)
    }
  }

  // Format the campaign status
  const getCampaignStatusText = () => {
    if (campaignInfo.status === 0) return "Created"
    if (campaignInfo.status === 1) return "Active"
    if (campaignInfo.status === 2) return "Ended"
    if (campaignInfo.status === 3) return "Canceled"
    return "Unknown"
  }

  // Get the appropriate status badge color
  const getStatusBadgeVariant = () => {
    if (campaignInfo.status === 0) return "outline" // Created
    if (campaignInfo.status === 1) return "success" // Active
    if (campaignInfo.status === 2) return "secondary" // Ended
    if (campaignInfo.status === 3) return "destructive" // Canceled
    return "outline"
  }

  return (
    <div className="mb-8">
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 mb-6">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-gray-100 mb-2">{campaignInfo.description}</h1>
          <div className="flex flex-wrap items-center gap-2 text-sm text-gray-600 dark:text-gray-400">
            <span className="flex items-center gap-1">
              <User className="h-4 w-4" />
              <span>
                Created by: {formatAddress(campaignInfo.creator)}
                {isOwner && <span className="ml-1 text-purple-700 dark:text-purple-400">(You)</span>}
              </span>
            </span>
            <span className="hidden md:inline">â€¢</span>
            <span className="flex items-center gap-1">
              <Calendar className="h-4 w-4" />
              <span>
                {new Date(campaignInfo.startTime * 1000).toLocaleDateString()} -
                {new Date(campaignInfo.endTime * 1000).toLocaleDateString()}
              </span>
            </span>
          </div>
        </div>

        <div className="flex flex-wrap items-center gap-2">
          <Badge variant={getStatusBadgeVariant()}>{getCampaignStatusText()}</Badge>
          <Badge variant="outline">{getVotingTypeText(campaignInfo.votingType)}</Badge>
          <Badge variant="outline">{getRestrictionText(campaignInfo.restriction)}</Badge>
        </div>
      </div>

      {/* Countdown Timer */}
      {(isBeforeStart || isDuringVoting) && (
        <div className="bg-white dark:bg-gray-900 rounded-lg p-4 shadow-sm border border-gray-200 dark:border-gray-800 mb-6">
          <div className="flex items-center gap-2 mb-2">
            <Clock className="h-5 w-5 text-purple-700 dark:text-purple-400" />
            <h2 className="font-medium text-gray-900 dark:text-gray-100">
              {isBeforeStart ? "Voting begins in:" : "Voting ends in:"}
            </h2>
          </div>
          <div className="grid grid-cols-4 gap-2 text-center">
            <div className="bg-gray-100 dark:bg-gray-800 p-2 rounded">
              <div className="text-2xl font-bold text-purple-700 dark:text-purple-400">{timeRemaining.days}</div>
              <div className="text-xs text-gray-600 dark:text-gray-400">Days</div>
            </div>
            <div className="bg-gray-100 dark:bg-gray-800 p-2 rounded">
              <div className="text-2xl font-bold text-purple-700 dark:text-purple-400">{timeRemaining.hours}</div>
              <div className="text-xs text-gray-600 dark:text-gray-400">Hours</div>
            </div>
            <div className="bg-gray-100 dark:bg-gray-800 p-2 rounded">
              <div className="text-2xl font-bold text-purple-700 dark:text-purple-400">{timeRemaining.minutes}</div>
              <div className="text-xs text-gray-600 dark:text-gray-400">Minutes</div>
            </div>
            <div className="bg-gray-100 dark:bg-gray-800 p-2 rounded">
              <div className="text-2xl font-bold text-purple-700 dark:text-purple-400">{timeRemaining.seconds}</div>
              <div className="text-xs text-gray-600 dark:text-gray-400">Seconds</div>
            </div>
          </div>
        </div>
      )}

      {/* Status Update Alert for Owner */}
      {isOwner && needsStatusUpdate && (
        <Alert className="mb-6 border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
          <AlertCircle className="h-4 w-4 text-amber-600 dark:text-amber-400" />
          <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2 flex-grow">
            Campaign status needs to be updated to reflect its current phase.
          </AlertDescription>
          <Button
            size="sm"
            onClick={updateCampaignStatus}
            disabled={isUpdatingStatus}
            className="ml-2 bg-amber-600 hover:bg-amber-700 text-white"
          >
            {isUpdatingStatus ? (
              "Updating..."
            ) : isDuringVoting ? (
              <span className="flex items-center gap-1">
                <PlayCircle className="h-4 w-4" /> Activate
              </span>
            ) : (
              <span className="flex items-center gap-1">
                <StopCircle className="h-4 w-4" /> End Campaign
              </span>
            )}
          </Button>
        </Alert>
      )}

      {/* Action Error */}
      {actionError && (
        <Alert className="mb-6 border-red-200 bg-red-50 dark:bg-red-950/20 dark:border-red-900">
          <AlertCircle className="h-4 w-4 text-red-600 dark:text-red-400" />
          <AlertDescription className="text-red-800 dark:text-red-300 ml-2">{actionError}</AlertDescription>
        </Alert>
      )}
    </div>
  )
}
